import { Plot } from '../../base';
import { adaptor } from './adaptor';

import type { BoxOptions } from './type';
import type { Adaptor } from '../../types';

export type { BoxOptions };

export class Box extends Plot<BoxOptions> {
  /**  */
  public type = 'box';

  /**
   * 
   * 
   */
  static getDefaultOptions(): Partial<BoxOptions> {
    return {
      type: 'view',
      children: [{ type: 'box' }],
      axis: {
        y: { title: false },
        x: { title: false },
      },
      //  tooltip
      tooltip: {
        items: [
          { name: 'min', channel: 'y' },
          { name: 'q1', channel: 'y1' },
          { name: 'q2', channel: 'y2' },
          { name: 'q3', channel: 'y3' },
          { name: 'max', channel: 'y4' },
        ],
      },
    };
  }

  /**
   *   
   */
  protected getDefaultOptions() {
    return Box.getDefaultOptions();
  }

  /**
   * 
   */
  protected getSchemaAdaptor(): (params: Adaptor<BoxOptions>) => void {
    return adaptor;
  }
}
